$btnColor: #fff;
$btnBackground: #FF485E;
body{
  padding:0;
  margin: 0;
  text-align: center;
  h1 {
    font-weight: lighter;
    margin: 50px auto;
    color: #4a4a4a;
    
  }
  #wrapper {
    width: 100vw;
    #wide{
      width: 100vw;
      display: flex;
      justify-content: center;
      align-items: center;
      #quote-box {
        font-family: tahoma;
        max-width: 700px;
        width: 70%;
        background: #eaeaea;
        text-align: center;
        font-family: tahoma;
        padding: 40px;
        #text {
          font-weight: bold;
          font-family: inherit;
          font-size: 1.70em;
        }
        #author{
          font-style: italic;
          font-size: 1em;
          padding: 30px 0;
          color: #9b9b9b;
        }
        #buttons{
          display: flex;
          justify-content: space-around;
          margin-top: 50px;
          button {
            padding: 10px 15px;
            border: 0;
            font-size: 0.85em;
            cursor: pointer;
            color: $btnColor;
            background: $btnBackground;
          }
          a {
            padding: 10px 10px;
            color: $btnColor;
            text-decoration: none;
            font-size: 0.85em;
            background:$btnBackground;
          }
        }
      }
    }
  }
}
